import React from "react";
import PropTypes from "prop-types";
import _ from "lodash";

import OraSwitcher from "../../../../core/components/ui/OraSwitcher";
import OfferPropositionDetails
    from "../../../../magnum2/components/OfferPropositionList/OfferProposition/OfferPropositionDetails";
import {fraction, integer} from "../../../../magnum2/components/Utils";

const MagnumOfferPropositionListItemProduct = (props) => {
    const payNowPrice = formatPrice(props.offer.payNowPrice.value, props.offer.currency);
    const monthlyPayments = formatPrice(props.offer.monthlyPayments[0].finalPrice, props.offer.currency);
    return (
        <OraSwitcher className="u-no-padding-l u-no-padding-r" isSelected={props.selected}>
            <div className="l-row u-no-margin u-padding-left-m ">
                <div className="l-col l-col-1 ">
                    <label className="o-radio opl-radio">
                        <input type="radio" name="offer-plans" className="switcher-trigger" checked={props.selected} onChange={props.onChange} disabled={props.disabled}/>
                        <span className="o-ci"/>
                        <span className="o-ci-label"/>
                    </label>
                </div>
                <div className="l-col l-col-11">
                    <div className="l-row u-small-block u-no-margin">

                        <div
                            className="l-col l-col-small-12 l-col-medium-6 l-col-6  u-small-block u-small-no-padding-r u-no-padding-l">
                            <div className="u-small-block u-small-right u-large-hide u-medium-hide">
                            </div>
                            <p className="h5 u-spacing">
                                {
                                    (!!props.selectedProposition.promotionType ?
                                        "Orange Love " + _.capitalize(props.selectedProposition.promotionType)
                                        : "")
                                    || props.selectedProposition.hardbundleName
                                }
                            </p>
                            <p dangerouslySetInnerHTML={{__html: props.selectedProposition.offerDescription}}/>
                            <div className="u-inline-block">
                                <OfferPropositionDetails linkName={props.descriptions.detailsLabel}
                                                         proposition={props.selectedProposition}
                                                         transactions={props.transactions}/>
                            </div>
                        </div>

                        <div
                            className="l-col l-col-small-12 l-col-medium-3 l-col-3  u-small-padding-top u-small-no-padding-r">
                            <div className="u-small-hide">
                                <p className="u-left">
                                    <span className="u-font-bold u-right">{monthlyPayments}</span>
                                </p>
                            </div>
                        </div>

                        <div
                            className="u-large-hide u-medium-hide l-col l-col-small-12 l-col-medium-3 l-col-3   u-small-block u-small-no-padding-r">

                            <div className="l-row">
                                <div className="l-col l-col-5 u-no-padding">
                                    <p className="u-medium-hide u-large-hide u-inline-block">Op≈Çata abonamentowa</p>
                                </div>
                                <div className="l-col l-col-7 u-small-right u-font-bold">
                                    <p className="u-left u-text-right">
                                        <span className="u-font-bold u-right">{monthlyPayments}</span>
                                    </p>
                                </div>
                            </div>
                        </div>

                        <div
                            className="u-small-hide l-col l-col-small-12 l-col-medium-3 l-col-3  u-small-no-padding-r ">
                            <div className="u-small-hide">
                                <p>
                                    <span className="u-font-bold u-right">{payNowPrice}</span>
                                </p>

                            </div>
                        </div>

                        <div
                            className="u-large-hide u-medium-hide l-col l-col-small-12 l-col-medium-3 l-col-3   u-small-block u-small-no-padding-r">

                            <div className="l-row">
                                <div className="l-col l-col-5 u-no-padding">
                                    <p className="u-medium-hide u-large-hide u-inline-block">Aktywacja</p>
                                </div>
                                <div className="l-col l-col-7 u-small-right u-font-bold">
                                    <p className="u-left u-text-right">
                                        <span className="u-font-bold u-right">{payNowPrice}</span>
                                    </p>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </OraSwitcher>


    );
};

MagnumOfferPropositionListItemProduct.propTypes = {
    descriptions: PropTypes.shape({
        detailsLabel: PropTypes.string
    }),
    offer: PropTypes.shape({
        currency: PropTypes.string,
        payNowPrice: PropTypes.shape({
            value: PropTypes.string
        }),
        monthlyPayments: PropTypes.arrayOf(PropTypes.shape({
            finalPrice: PropTypes.string
        }))
    }),
    selected: PropTypes.bool,
    selectedProposition: PropTypes.object,
    onChange: PropTypes.func,
    offerDescription: PropTypes.string
};

function formatPrice(price, currency) {
    return '' + integer(price) + ',' + fraction(price) + ' ' + currency;
}

export default MagnumOfferPropositionListItemProduct;